import './App.css';
import {BrowserRouter as Router, Route} from 'react-router-dom' 




import HomePage from './pages/HomePage';
import BroadbandPage from './pages/BroadbandPage';
import InsurancePage from './pages/InsurancePage';
import BankingPage from './pages/BankingPage';
import AdminPage from './pages/AdminPage';




function App() {
  return (
    <div className="App">
    <Router>
      
	      
        <Route component={HomePage} path="/"exact />
        <Route component={BroadbandPage} path="/broadband"exact />
        <Route component={InsurancePage} path="/insurance"exact />
        <Route component={BankingPage} path="/banking"exact />
        <Route component={AdminPage} path="/admin"exact />


    </Router>
    </div>
  );
}

export default App;
